import { Component, OnInit, ViewChild } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatCardModule } from '@angular/material/card';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatInputModule } from '@angular/material/input';
import { MatIconModule } from '@angular/material/icon';
import { MatButtonModule } from '@angular/material/button';
import { MatTableModule, MatTableDataSource } from '@angular/material/table';
import { MatPaginator, MatPaginatorModule } from '@angular/material/paginator';
import { MatSort, MatSortModule } from '@angular/material/sort';
import { MatSnackBar, MatSnackBarModule } from '@angular/material/snack-bar';
import { FormBuilder, FormGroup, ReactiveFormsModule, Validators } from '@angular/forms';
import { FichaService } from '../../services/ficha.service';
import { FichaEntity } from '../../models/ficha.model';
import { Router, RouterModule } from '@angular/router';
import { MatExpansionModule } from '@angular/material/expansion';
import { AuthService } from '../../services/auth.service';

@Component({
  selector: 'app-fichas',
  standalone: true,
  imports: [
    CommonModule,
    MatCardModule,
    ReactiveFormsModule,
    MatFormFieldModule,
    MatInputModule,
    MatIconModule,
    MatButtonModule,
    MatTableModule,
    MatPaginatorModule,
    MatSortModule,
    MatSnackBarModule,
    RouterModule,
    MatExpansionModule,
  ],
  templateUrl: './ficha.component.html',
  styleUrls: ['./ficha.component.scss'],
})
export class FichaComponent implements OnInit {
  displayedColumns: string[] = [
    'idFicha',
    'codUnico',
    'estado',
    'departamento',
    'provincia',
    'distrito',
    'nomEntidad',
    'nomUnidad',
    'acciones',
  ];
  dataSource = new MatTableDataSource<FichaEntity>();

  fichaForm: FormGroup; // Formulario reactivo para registrar/editar fichas
  ficha: FichaEntity[] = []; // Lista de fichas asignadas
  isEditing: boolean = false; // Bandera para saber si estamos editando

  pageSizeOptions: number[] = [5, 10, 20, 50];
  defaultPageSize: number = 20;
  
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  constructor(
    private fichaService: FichaService,
    private fb: FormBuilder,
    private snackBar: MatSnackBar,
    private router: Router,
    private authService: AuthService
  ) {
    // InicializaciÃ³n del formulario reactivo
    this.fichaForm = this.fb.group({
     // idFicha: [null], // Campo opcional para ediciÃ³n
      idFicha: ['', [Validators.required, Validators.pattern('^[0-9]+$')]],
      codUnico: ['', [Validators.required, Validators.maxLength(20)]],
      nomEntidad: ['', [Validators.required, Validators.maxLength(500)]],
      nomUnidad: ['', [Validators.required, Validators.maxLength(500)]],
      estado: ['', [Validators.required, Validators.maxLength(1)]],
      departamento: ['', [Validators.required, Validators.maxLength(100)]],
      provincia: ['', [Validators.required, Validators.maxLength(100)]],
      distrito: ['', [Validators.required, Validators.maxLength(100)]],
    });
  }




tieneRolEspecialista(): boolean {
  return this.authService.getRoles().includes('ESPECIALISTA');
}

  ngOnInit(): void {
    this.cargarFichas();
  }

  cargarFichas(): void {
    this.fichaService.listarFichas().subscribe({
      next: (fichas) => {
        this.dataSource.data = fichas;
        //this.asignaciones = fichas; // Asignar las fichas a la lista de asignaciones
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      },
      error: () => {
        this.snackBar.open('Error al cargar las fichas', 'Cerrar', {
          duration: 3000,
          horizontalPosition: 'right',
          verticalPosition: 'top',
          panelClass: ['snackbar-error'],
        });
      },
    });
  }

  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value.trim().toLowerCase();
    this.dataSource.filter = filterValue;

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  registrarFicha(): void {
    if (this.fichaForm.valid) {
      this.fichaService.registrarFicha(this.fichaForm.value).subscribe({
        next: () => {
          this.cargarFichas();
          this.fichaForm.reset();
          this.snackBar.open('Ficha registrada correctamente', 'Cerrar', {
            duration: 3000,
            horizontalPosition: 'right',
            verticalPosition: 'top',
            panelClass: ['snackbar-success'],
          });
        },
        error: () => {
          this.snackBar.open('Error al registrar la ficha', 'Cerrar', {
            duration: 3000,
            horizontalPosition: 'right',
            verticalPosition: 'top',
            panelClass: ['snackbar-error'],
          });
        },
      });
    }
  }

  // editarFicha(ficha: FichaEntity): void {
  //   // Carga los datos de la ficha seleccionada en el formulario
  //   this.fichaForm.patchValue(ficha);
  // }


  // editarFicha(ficha: FichaEntity): void {
  //   localStorage.setItem('idFichaPantbc', ficha.idFicha.toString());
  //   this.router.navigate(['/dashboard/registrar-ficha']); // redirige al formulario y antes pasa al  (ngOnInit) del componente registrar-ficha
  // }
  

//   editarFicha(ficha: FichaEntity): void {
//   localStorage.setItem('idFichaPantbc', ficha.idFicha.toString()); // âœ… Guarda el nuevo ID
//   localStorage.setItem('fichaPantbc', JSON.stringify(ficha));      // âœ… Guarda data general
//   this.router.navigate(['/dashboard/registrar-ficha']);            // âœ… Redirige al formulario
// }

editarFicha(ficha: FichaEntity): void {
  // ðŸ’£ Limpia antes de guardar
  localStorage.removeItem('idFichaPantbc');
  localStorage.removeItem('fichaPantbc');

  // âœ… Guarda ID y datos generales
  localStorage.setItem('idFichaPantbc', ficha.idFicha.toString());
  localStorage.setItem('fichaPantbc', JSON.stringify(ficha));

  // âœ… Limpia tambiÃ©n las secciones si las vas a recargar desde backend
  for (let i = 1; i <= 7; i++) {
    localStorage.removeItem(`pantbc_s${i}`);
  }
  localStorage.removeItem('pantbc_completadas');

  // ðŸ” Redirige
  this.router.navigate(['/dashboard/registrar-ficha']);
}



  limpiarFichaPantbc(): void {
    localStorage.removeItem('fichaPantbc');
    localStorage.removeItem('idFichaPantbc');
    localStorage.removeItem('pantbc_secciones');
    localStorage.removeItem('pantbc_s1'); 
  }



  getEstadoFichaVisual(estado: 'C' | 'I' | null): { texto: string, clase: string } {
  switch (estado) {
    case 'C':
      return { texto: 'Ficha completa', clase: 'badge bg-success' };
    case 'I':
      return { texto: 'Ficha incompleta', clase: 'badge bg-warning text-dark' };
    default:
      return { texto: 'Sin estado', clase: 'badge bg-secondary' };
  }
}


  eliminarFicha(idFicha: number): void {
    if (confirm('Â¿EstÃ¡s seguro de que deseas eliminar esta ficha?')) {
      this.fichaService.eliminarFicha(idFicha).subscribe({
        next: () => {
          this.cargarFichas();
          this.snackBar.open('Ficha eliminada correctamente', 'Cerrar', {
            duration: 3000,
            horizontalPosition: 'right',
            verticalPosition: 'top',
            panelClass: ['snackbar-success'],
          });
        },
        error: () => {
          this.snackBar.open('Error al eliminar la ficha', 'Cerrar', {
            duration: 3000,
            horizontalPosition: 'right',
            verticalPosition: 'top',
            panelClass: ['snackbar-error'],
          });
        },
      });
    }
  }
}




